define(['jquery', 'core/templates', 'core/str'], function($, Templates, Str) {

    var init = function() {
        if ($('body').hasClass('ie11')) {
            var pageWrapper = $('#arup-page-wrapper');

            Str.get_string('browseralert', 'theme_arupboost').done(function(s) {
                Templates.render('theme_arupboost/browseralert', {text: s})
                .then(function(html) {
                    pageWrapper.prepend(html);
                });
            });
        }
    };

    return {
        init: init
    };
});